import React from 'react';
import { connect } from 'react-redux'
import { filterDrinks } from '../../drinks/drinks-actions'

const stateToProps = state => {
    const { drinksList } = state.drinks
    return {
        nameOrIng: drinksList.nameOrIng
    };
};

function dispatchToProps(dispatch) {
    return {
        filterDrinks: nameOrIng => dispatch(filterDrinks(nameOrIng))
    };
}

function DrinksFilter({ nameOrIng, filterDrinks }) {

    return (<>

        {/* <input type="text"
            value={nameOrIng}
            onChange={event => {
                const currVal = event.target.value
                if (currVal !== nameOrIng) {
                    filterDrinks(currVal)
                }
                event.stopPropagation()
            }}
            className='drinks-filter'
            placeholder='nazwa drinka lub składnika'></input> */}

        <button
            className='drinks-filter'>Wyszukaj po nazwie lub składniku</button>

        <button onClick={() => filterDrinks('')}
            disabled={!nameOrIng}
            className='drinks-filter'>Wyczyść</button>

    </>)
}

export default connect(stateToProps, dispatchToProps)(DrinksFilter)